import { Theme, withStyles, WithStyles, StyleRulesCallback } from "material-ui/styles";
import * as React from "react";
import withRoot from "../../withRoot";
import { AppBar, Toolbar, IconButton, Typography, Hidden, Drawer } from "material-ui";
import ResponsiveDrawer from "./ResponsiveDrawer";
import MenuIcon from "material-ui-icons/Menu";

const drawerWidth = 240;

const style: StyleRulesCallback<"root"
        | "appBar"
        | "navIconHide"
        | "toolbar"
        | "drawerPaper"
        | "content"> = theme => ({
    root: {
        flexGrow: 1,
        height: 430,
        zIndex: 1,
        overflow: 'hidden',
        position: 'relative',
        display: 'flex',
        width: '100%',
      },
      appBar: {
        position: 'absolute',
        marginLeft: drawerWidth,
        [theme.breakpoints.up('md')]: {
          width: `calc(100% - ${drawerWidth}px)`,
        },
      },
      navIconHide: {
        [theme.breakpoints.up('md')]: {
          display: 'none',
        },
      },
      toolbar: theme.mixins.toolbar,
      drawerPaper: {
        width: drawerWidth,
        [theme.breakpoints.up('md')]: {
          position: 'relative',
        },
      },
      content: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.default,
        padding: theme.spacing.unit * 3,
      },
});

type Props = {
};

type PropsWithStyles = Props & WithStyles<"root"
| "appBar"
| "navIconHide"
| "toolbar"
| "drawerPaper"
| "content">;



const LayoutMui: React.SFC<PropsWithStyles> = ({
  classes,
  ...props
}: PropsWithStyles) => (
  <div>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"

               onClick={() => { }}
              className={classes.navIconHide}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="title" color="inherit" noWrap>
              Responsive drawer
            </Typography>
          </Toolbar>
        </AppBar>
        <Hidden mdUp>
          <Drawer
            variant="temporary"
            itemRef="ResponsiveDrawer"
            anchor={'left'}
             open={false}
            // onClose={this.handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {<ResponsiveDrawer />}
          </Drawer>
        </Hidden>
        <Hidden smDown implementation="css">
          <Drawer
            variant="permanent"
            itemRef="ResponsiveDrawer"
            open={true}
            classes={{
              paper: classes.drawerPaper,
            }}
          >
            {<ResponsiveDrawer />}
          </Drawer>
        </Hidden>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Typography noWrap>{'You think water moves fast? You should see ice.'}</Typography>
        </main>
  </div>
);

export default withRoot(withStyles(style)<{}>(LayoutMui));


