import { Theme, withStyles, WithStyles, StyleRulesCallback } from "material-ui/styles";
import * as React from "react";
import withRoot from "../../withRoot";
import { Drawer, List, ListItem, ListItemIcon, ListItemText } from "material-ui";
import SupervisorAccountIcon from "material-ui-icons/SupervisorAccount";
import DashboardIcon from "material-ui-icons/Dashboard";
import HomeIcon from "material-ui-icons/Home";
import AssigmentIdenIcon from "material-ui-icons/AssignmentInd";
import AccountBoxIcon from "material-ui-icons/AccountBox";
import Collapse from 'material-ui/transitions/Collapse';
import ListSubheader from 'material-ui/List/ListSubheader';
import ExpandLess from 'material-ui-icons/ExpandLess';
import ExpandMore from 'material-ui-icons/ExpandMore';
import AssignmentIcon from "material-ui-icons/Assessment";
import TimelineIcon from "material-ui-icons/Timeline";
import ListNumberedIcon from "material-ui-icons/FormatListNumbered";
import { Link } from "react-router-dom";

const drawerWidth = 240;

const style: StyleRulesCallback<"nested"> = theme => ({
    nested: {}
});

type Props = {

};

type PropsWithStyles = Props & WithStyles<"nested">;


const ResponsiveDrawer: React.SFC<PropsWithStyles> = ({
  classes,
  ...props
}: PropsWithStyles) => (

  <div>
       <Drawer>
       <List>
          <div>
            <ListItem button>
              <ListItemIcon>
                <HomeIcon />
              </ListItemIcon>
              <ListItemText primary="Home" />
            </ListItem>
            <ListItem button>
              <ListItemIcon>
                <AssigmentIdenIcon />
              </ListItemIcon>
              <ListItemText primary="Survey" />
              {true ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={true} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                <Link to="/survey">
                  <ListItem button className={classes.nested}>
                    <ListItemIcon>
                      <AssignmentIcon />
                    </ListItemIcon>
                    <ListItemText inset primary="Developer's survey" />
                  </ListItem>
                </Link>
                <Link to="/respondents/all">
                  <ListItem button className={classes.nested}>
                    <ListItemIcon>
                      <ListNumberedIcon />
                    </ListItemIcon>
                    <ListItemText inset primary="Respondents" />
                  </ListItem>
                </Link>
                <Link to="/statistics">
                  <ListItem button href="/statistics" className={classes.nested}>
                    <ListItemIcon>
                      <AssignmentIcon />
                    </ListItemIcon>
                    <ListItemText inset primary="Statistics" />
                  </ListItem>
                </Link>
              </List>
            </Collapse>
            <ListItem button>
              <ListItemIcon>
                <DashboardIcon />
              </ListItemIcon>
              <ListItemText primary="Admin Area" />
              {true ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={true} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                <ListItem button className={classes.nested}>
                  <ListItemIcon>
                    <SupervisorAccountIcon />
                  </ListItemIcon>
                  <ListItemText inset primary="Users" />
                </ListItem>
              </List>
            </Collapse>
            <ListItem button>
              <ListItemIcon>
                <AccountBoxIcon />
              </ListItemIcon>
              <ListItemText primary="Login" />
            </ListItem>
          </div>
        </List>
       </Drawer>
  </div>
);

export default withRoot(withStyles(style)<{}>(ResponsiveDrawer));


